import * as React from 'react';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import VpnKeyIcon from '@mui/icons-material/VpnKey';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import Paper from '@mui/material/Paper';
import Profile from "./Pages/Profile"


export default function Home() {
    const [value, setValue] = React.useState(2);
    const ref = React.useRef(null);
    const [UserId,setUserId] = React.useState(0)
    React.useEffect(() => {
        try {
            const initData = window.Telegram.WebApp.initData.split("&")
            const query_id = initData[0].split("query_id=")[1]
            const user_info = JSON.parse(decodeURIComponent(initData[1].split("user=")[1]))
            const auth_date = initData[2].split("auth_date=")[1]

            setUserId(user_info.id)
        }catch (e) {
            window.alert("Cant read!")
            window.alert(e)
        }
    }, []);



    return (
        <Box sx={{ pb: 7 }} ref={ref}>
            <CssBaseline />

            <Profile user_id={UserId}></Profile>
            <Paper sx={{ position: 'fixed', bottom: 0, left: 0, right: 0 }} elevation={3}>
                <BottomNavigation
                    showLabels
                    value={value}
                    onChange={(event, newValue) => {
                        setValue(newValue);
                    }}
                >
                    <BottomNavigationAction label="خرید سرویس" icon={<ShoppingCartIcon />} />
                    <BottomNavigationAction label="سرویس ها" icon={<VpnKeyIcon />} />
                    <BottomNavigationAction label="پروفایل" icon={<AccountCircleIcon />} />
                </BottomNavigation>
            </Paper>
        </Box>
    );
}
